<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">产品管理</span>
        <el-button type="success" @click="openAddDialog()">新增</el-button>
        <!--<el-button
          type="text"
          style="float: right; padding: 3px 0"
          @click="moreSelect"
        ><i :class="selectClass"></i>更多选项</el-button>-->
        <div class="advanced-box" v-if="content_show">
          <el-form :inline="true" :model="forms" class="demo-form-inline">
            <el-form-item label="类型">
              <el-select v-model="forms.product_type" multiple placeholder="请选择" style="width:100%">
                <el-option
                  v-for="item in product_type_list"
                  :key="item.name"
                  :label="item.name"
                  :value="item.name"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="reset">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </el-row>
    <el-table border :data="tableData">
      <el-table-column prop="index" label="序号" align="center" width="60"></el-table-column>
      <!--<el-table-column
        prop="product_type"
        label="类型"
        align="center"
      >
      </el-table-column>-->
      <el-table-column prop="title" label="标题" align="center"></el-table-column>
      <el-table-column prop="icon_src" label="icon" align="center" width="90">
        <template slot-scope="scope">
          <img :src="scope.row.icon_src" alt style="width: 50px;height: 50px">
        </template>
      </el-table-column>
      <!-- <el-table-column prop="is_jump_name" label="是否跳过" align="center" width="90"></el-table-column> -->
      <el-table-column prop="status_name" label="状态" align="center" width="90"></el-table-column>
      <el-table-column prop="release_time" label="发布时间" align="center" width="160"></el-table-column>
      <el-table-column prop="update_time" label="修改时间" align="center" width="160"></el-table-column>
<!-- 
      <el-table-column prop="update_user" label="修改者" align="center" width="110"></el-table-column> -->

      <!--<el-table-column
        prop="link"
        label="链接地址"
        align="center"
      >
      </el-table-column>-->
      <el-table-column label="操作" align="center" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
           <el-button
            size="mini"
            v-clipboard:copy="'/pages/productHot/productHot?id='+scope.row.product_id"
            v-clipboard:success="onCopy"


          >复制页面</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block pager">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[15, 20, 30]"
        :page-size="page_size"
        layout="sizes, prev, pager, next"
        :total="total"
      ></el-pagination>
    </div>
    <el-dialog
      title="新增"
      :visible.sync="dialogVisible"
      width="60%"
      top="5vh"
      :append-to-body="true"
      :before-close="handleClose"
      :closeOnClickModal="false"
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <!--<el-form-item label="类型">
            <el-select
              v-model="formLabelAlign.product_type"
              multiple
              placeholder="请选择"
              style="width:100%"
            >
              <el-option
                v-for="item in product_type_list"
                :key="item.name"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>-->
          <el-form-item label="标题" prop="title">
            <el-input v-model="formLabelAlign.title"></el-input>
          </el-form-item>
          <el-form-item label="icon图" prop="ico_name">
            <el-col :span="20">
              <el-input v-model="formLabelAlign.ico_name" :disabled="true"></el-input>
            </el-col>
            <el-upload
              class="upload-demo"
              action="core/file/upload"
              multiple
              :limit="1"
              :before-upload="beforeUpload"
              :show-file-list="false"
            >
              <el-button size="medium" type="primary">点击上传</el-button>
            </el-upload>
          </el-form-item>
          <div class="editor-container">
            <ueditor
              :defaultMsg="formLabelAlign.content"
              @postEditorToParent="getEditorContent"
              :config="config"
              ref="ueditor"
            ></ueditor>
          </div>
          <el-form-item label="描述" prop="summary" style="margin-bottom:0;">
            <el-input v-model="formLabelAlign.summary"></el-input>
          </el-form-item>
          <!-- <el-form-item label="是否跳过">
            <el-radio-group v-model="formLabelAlign.is_jump">
              <el-radio :label="0">否</el-radio>
              <el-radio :label="1">是</el-radio>
            </el-radio-group>
          </el-form-item>-->
          <el-form-item label="官网地址">
            <el-input v-model="formLabelAlign.url"></el-input>
          </el-form-item>
          <el-form-item label="联系方式">
            <el-input v-model="formLabelAlign.email"></el-input>
          </el-form-item>
          <el-form-item label="源链接">
            <el-input v-model="formLabelAlign.link"></el-input>
          </el-form-item>
          <el-form-item label="状态">
            <el-radio-group v-model="formLabelAlign.status">
              <el-radio :label="0">不显示</el-radio>
              <el-radio :label="1">显示</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="发布时间" prop="release_time">
            <el-date-picker
              v-model="formLabelAlign.release_time"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择日期"
              default-time="01:01:01"
            ></el-date-picker>
          </el-form-item>
          <el-row :gutter="10">
            <el-col :span="10">
              <el-form-item label="创建者" prop="create_user">
                <el-input v-model="formLabelAlign.create_user" :disabled="true"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item label="创建时间">
                <el-input v-model="formLabelAlign.create_time" :disabled="true"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="addInfo('formLabelAlign')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { authToken, authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
import { prefix } from "@/api";
export default {
  data() {
    return {
      auth_name: "",
      selectClass: "el-icon-arrow-down",
      content_show: false,
      forms: {
        product_type: []
      },
      //表格
      tableData: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        title: "",
        content: "",
        summary: "",
        icon: "",
        ico_name: "",
        status: 0,
        url: "",
        emial: "",
        is_jump: 0,
        product_type: [],
        release_time: "",
        create_user: "",
        create_time: "",
        link: ""
      },
      rules: {
        title: [{ required: true, message: "请输入标题", trigger: "blur" }],
        release_time: [
          { required: true, message: "请选择时间", trigger: "blur" }
        ]
      },
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350,
        serverUrl:
          prefix +
          "core/file/upload?token=" +
          storageHelper.getItem(authToken, "healthcare"),
        imageActionName: prefix + "core/file/upload",
        zIndex: 10000
      },
      product_type_list: [],
      token: ""
    };
  },
  mounted() {
    this.auth_name = storageHelper.getItem(authName, "healthcare");
    this.token = storageHelper.getItem(authToken, "healthcare");
    this.getInfoList("1", "15");
    this.getDictionaryInfo();
  },
  methods: {
    handleSizeChange(val) {
      this.getInfoList(this.currentPage, val);
    },
    handleCurrentChange(val) {
      this.getInfoList(val, this.page_size);
    },
    handleClose() {
      var that = this;
      this.$confirm("是否保存？", {
        showClose: false
      })
        .then(() => {
          that.addInfo("formLabelAlign");
        })
        .catch(() => {
          this.dialogVisible = false;
        });
    },
    openAddDialog() {
      this.formLabelAlign = {
        title: "",
        content: "",
        summary: "",
        icon: "",
        ico_name: "",
        status: 0,
        url: "",
        emial: "",
        is_jump: 0,
        product_type: [],
        release_time: "",
        create_user: this.auth_name,
        create_time: getCurTime(),
        link: ""
      };
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      var that = this;
      var fd = new window.FormData();
      fd.append("file", file);
      axios
        .post("core/file/upload", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: this.token
          }
        })
        .then(function(res) {
          if (res.code == 0) {
            var url = res.data.url;
            that.formLabelAlign.ico_name = file.name;
            that.formLabelAlign.icon = res.data.code;
            that.$message({
              type: "success",
              message: "上传成功！"
            });
          }
          //console.log(res);
        });
      return false; // 返回false不会自动上传
    },
    moreSelect() {
      if (this.selectClass == "el-icon-arrow-down") {
        this.selectClass = "el-icon-arrow-up";
        this.content_show = true;
      } else {
        this.selectClass = "el-icon-arrow-down";
        this.content_show = false;
      }
    },
    //查询
    onSubmit() {
      this.getInfoList("1", "15");
    },
    //重置
    reset() {
      this.forms = {
        product_type: []
      };
      this.getInfoList("1", "15");
    },
    //编辑
    handleEdit(index, row) {
      this.dialogVisible = true;
      var product_type = row.product_type.split(",");
      this.formLabelAlign = {
        product_id: row.product_id,
        title: row.title,
        content: row.content,
        summary: row.summary,
        icon: row.icon,
        ico_name: row.ico_name,
        url:row.url,
        email:row.email,
        is_jump: row.is_jump,
        status: row.status,
        product_type: product_type,
        release_time: row.release_time,
        create_user: row.create_user,
        create_time: row.create_time,
        link: row.link
      };
    },
    //删除
    handleDelete(index, row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          var params = { product_id: row.product_id };
          that.submitInfo("app/product/delete", params, "删除成功!");
        })
        .catch(() => {});
    },
     onCopy(e) {
      alert("复制成功")
    },
    //编辑框提交按钮
    addInfo(formType) {
      var that = this;
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("product_id")) {
            method = "app/product/update";
            info = "修改成功!";
          } else {
            method = "app/product/create";
            info = "添加成功!";
          }
          that.formLabelAlign.product_type = that.formLabelAlign.product_type.join(
            ","
          );
          that.submitInfo(method, that.formLabelAlign, info);
        } else {
          return false;
        }
      });
    },
    getEditorContent(content) {
      this.formLabelAlign.content = content;
    },
    getInfoList(page_index, page_size) {
      var params = {
        product_type: this.forms.product_type.join(","),
        page_index: page_index,
        page_size: page_size
      };
      var that = this;
      axios.get("app/product/pageList", { params }).then(
        function(response) {
          var code = response.code;
              console.log(response)
          if (0 == code) {
            var array = [];
            var rdata = response.data;
            var count = rdata.count;
            var page_index = rdata.page_index;
            var page_size = rdata.page_size;
            that.currentPage = page_index;
            that.page_size = page_size;
            that.total = count;
            var des_num = (page_index - 1) * page_size;
            var json = rdata.rows;
            for (var i = 0; i < json.length; i++) {
              var status = json[i].status;
              var is_jump = json[i].is_jump;
              var icon_src = "";
              var ico_name = "";
              if (null != json[i].icon_info) {
                icon_src = json[i].icon_info.src;
                ico_name = json[i].icon_info.name;
              }
              json[i].create_time = utcTimeChange(json[i].create_time);
              json[i].release_time = utcTimeChange(json[i].release_time);
              json[i].update_time = utcTimeChange(json[i].update_time);
              var status_name = "显示";
              if (status == 0) {
                status_name = "不显示";
              }
              var is_jump_name = "是";
              if (is_jump == 0) {
                is_jump_name = "否";
              }
              json[i].index = des_num + i + 1;
              json[i].status_name = status_name;
              json[i].is_jump_name = is_jump_name;
              json[i].icon_src = icon_src;
              json[i].ico_name = ico_name;
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    submitInfo(method, params, info) {
      var that = this;
      console.log(params)
      axios.post(method, params).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getInfoList(that.currentPage, that.page_size);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {}
      );
    },
    getDictionaryInfo() {
      var that = this;
      var params = { code: "product_type" };
      axios.get("core/dictionary/list", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.product_type_list = response.data;
          } else {
          }
        },
        function(error) {}
      );
    }
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


